*{
  margin:0;
  padding: 0;
  font: serif;
}

body{
  padding-top: 160px;
}
.App {
  text-align: center;
}
header{
  display: block;
  border-bottom: 1px solid lightgrey;
  padding: 20px;
  position: fixed;
  top:45px;
  width:100%;
  background-color: #fff;
  height: 115px;
}
header div, header form, header ul{
    display: inline-block;
    margin: auto;
    text-align: center;
}
header div, .logo-area{
  display: flex;
}
header div img, header div div, .logo-area{
  display: inline-block;
  margin: auto;
  text-align:left;
  width: 20%;
}
header div div{
  width: 80%;
  margin-left: 20px;
}
.logo-area div{
  margin: 10px 0 0 20px;
}
.logo-area img{
  margin-top: -30px;
}
header div img, .logo-area img{
  width: 80px;
  height: 50px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  border-radius: 0%;
}
header ul li {
  text-align: center;
  display: inline-block;
  list-style-type: none;
  margin-right: 30px;
  cursor: pointer;
  color:rgba(100,100,100,1);
}
header ul li:hover{
  color: rgb(0, 140, 255);
}
header ul li i{
  font-size: 1.4em;
}

header form input[type="search"]{
  border-radius: 5px 0 0 5px;
  padding: 6px 20px;
  background-color: rgba(240,240,240,1);
  border:none !important;
  outline:none;
  box-shadow: none;
  width:80%;
  cursor: pointer;
}
header form button{
  cursor:pointer;
  border-radius: 0px 5px 5px 0px;
  background-color: rgba(248, 244, 2, 0.938);
  color: rgb(0, 140, 255);
  padding: 6px 10px;
  border:none;
  outline:none;
  box-shadow: none;
}
.main{
  width:calc(100% - 600px);
  margin: auto;
  min-height: 100%;
  padding: 40px 20px 20px 20px;
}
.main .per{
  display: flex;
  background: transparent;
  color: #000;
  text-align: left;
  box-sizing: content-box;
  box-shadow: 2.5px 3px 10px lightgrey;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 20px;

}
.main .per img{
top:0;

position: inherit;
box-sizing: content-box;
display: inline-block;
width: 100px;
height:100px;
border-radius: 10px;
}
.main .per div{
  padding: 0 0 0 20px;
display: inline-block;
width: 80%;
}
.main .per div h6{
  text-transform: capitalize;
}
.main .per div sup, .main .per div sup del{
  font-size: .8em;
  color: rgba(120,120,120,1);
}
.main .per div button{
  font-weight: 500;
  padding: 3px 10px;
  background-color: transparent;
  border: 1px solid rgb(0, 140, 255);
  color: rgb(0, 140, 255);
}
.main .per div button:hover{
  transition: .3s ease-in-out;
  background-color: rgb(0, 140, 255);
  color: #fff;
}
.main-menu{
  height: 200px;
  margin: 0px 0 100px 0;
  padding-top: 20px;
  display: block;
  position: relative;
}
.main-menu li{
  display: inline-block;
  list-style-type: none;
  margin: auto;
  padding: 20px;
  width: 22%;
  color: rgba(100,100,100,1);
}
.main-menu li:hover{
  transition: .3s ease-in-out;
  color: rgb(0, 140, 255);
  cursor: pointer;
}
.main-menu li i{
  display: block;
  font-size: 3em;
  
}
.side-nav {
  border-right: 1px solid lightgrey;
  position: fixed;
  left: 0;
  width:300px;
  height: 100%;
}
.side-nav ul .active, .side-nav ul li:hover{
  font-weight: bold;
  color: rgb(0, 140, 255);
  border-right: 3px solid rgb(0, 140, 255);
  background: linear-gradient(to right, white, 85%, rgba(10,100,200,.21));
}
.side-nav ul b{
  color: rgb(0, 140, 255);
  cursor: pointer;
}
.side-nav ul b:hover{
  text-decoration: underline;
}
.side-nav ul{
  margin-left: auto;
  width: 240px;
  color:#222;
  text-align: left;
  padding: 20px 0 20px 20px;
}
.side-nav ul small::after{
  content: '';
  position: absolute;
  width: 150px;
  right:0;
  margin-top: 14px;
  height: 1px;
  background: lightgrey;
}
.side-nav ul li{
  color: rgba(100,100,100,1);
  cursor: pointer;
  text-transform: capitalize;
  padding: 10px 0;
  display: block;
}

.side-left {
  text-align: left;
  border-left: 1px solid lightgrey;
  position: fixed;
  margin-top: 0;
  right: 0;
  width:300px;
  height: 100%;
  display: none;
}
.side-left-sm {
  text-align: left;
  border-left: 1px solid lightgrey;
  position: fixed;
  margin-top: 0;
  right: 0;
  width:15px;
  height: 100%;
  display: none;
}
.side-left i[class="fas fa-minus"]:hover, .side-left-sm i[class="fas fa-plus"]:hover {
  color:rgb(0, 140, 255);
  cursor: pointer;
}
.info{
  height: 45px;
  padding: 10px 20px;
  background-color: rgb(0, 140, 255);
  color: white;
  text-align: left;
  position: fixed;
  width:100% !important;
  top:0;
}
.info a{
  color: white;
}
.properties{
  display: block;
  margin: 150px 0;
  background: linear-gradient(to right,silver, 70%, rgb(0, 140, 255));
  padding: 50px 20px;
  color: #fff;
  border-radius: 5px;
}
.properties ul li{
  list-style-type: none;
  display: inline-block;
  margin: auto;
  text-transform: capitalize;
  width: 30%;
  font-size: .9em;
}
.properties ul li i{
  font-size: 3em;
  color: rgba(248, 244, 2, 1);
  display: block;
  margin-bottom: 10px;
}

.bold{
    font-weight: bold;
 }

 header ul .l-search {
  display: none;
}
 .side-left .cart{
   margin-top: 30px;
   text-align: center;
 }
 .side-left .cart i{
   font-size: 5em;
   color:rgba(100,100,100,1);
   margin-bottom: 10px;
 }
footer{
  height: 200px;
  background-color: transparent;
}
footer div img{
width: 90px;
height: 50px;
}
footer div ul li{
  list-style-type: none;
  font-size: .9em;

} 
footer span{
  width: 100%;
  border-top: 1px solid lightgrey;
  text-align: center;
  padding: 5px;
  margin-top: 30px;
}
footer .fas, footer h6{
  color: rgb(0, 140, 255);
}
.support{
  position: fixed;
  right:30px;
  bottom:40px;
  border-radius: 100px 100px 0 100px;
  padding: 5px 15px 7px 15px;
  background: green;
  color: #fff;
  border: 1px solid green;
  cursor: pointer;
  transition: .3s ease;
}
.support:hover{
  color: green;
  background: transparent;
}
.app-download button{
border: 1px solid rgb(0, 140, 255);
color: rgb(0, 140, 255);
background: transparent;
padding: 5px 15px;
margin: 20px 0 0 20px;
transition: .3s ease;
}
.app-download button:hover{
  color: #fff;
background: rgb(0, 140, 255);
}
.app-download button i{
  margin-right: 10px;
}

.owl-carousel {
  position: inherit;
height: 100px;
border-radius: 5px;
background-color: transparent;
margin: 20px 0 100px 0;
}
.caro-div{
  background-color: rgb(0, 140, 255);
  height:150px;
}


.owl-dot{border: none !important;outline: none !important;box-shadow: none !important;padding: 0;height: 10px;margin: 20px 0 0 0;}
.owl-dot span{align-self: center; margin: 0px 9px;-webkit-outline: none;-ms-outline: none;-moz-outline: none;-o-outline: none;outline: none;border: none;box-shadow: none;padding: 0;}
.owl-dot span::after, .owl-dots .active::after{content: '';width: 10px; height:10px;border-radius: 100%;border:1.5px solid grey;position: absolute;margin-left:-10px;}

.owl-dots .active::after{border:5px solid grey;margin-left:-19px;}
.owl-dots .active{border: none !important;outline: none !important;box-shadow: none !important;padding: 0;}
