 @media (max-width: 991px){
  .main{
    margin: auto 0 auto auto !important;
    width:calc(100% - 300px);
  }
  .main-menu li i, .properties ul li i{
    font-size: 2em;
  }
  header form, header div div, .logo-area div{
    display: none !important;
  }
  header ul .l-search {
    display: inline-block;
  }
  header div img{margin:0 !important;}
  header ul{text-align: right;}
}
@media (min-width: 991px){
  .side-left, .side-left-sm{
    display: block;
  }

}
 @media (max-width: 820px){
   header{
    padding: 10px;
   }
  header ul li span{
  display: none;
  }
  .main{
    width:100%;
  }
  .side-left, .side-nav{
    display: none;
  }
 }
